.main-page {
  overflow: hidden;
}

.main {
  position: relative;
}

.main__slider .swiper-slide {
  height: 760px;
  padding-top: 105px;
  position: relative;

  &:after {
    content: "";
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 550px;
    background: linear-gradient(180deg, rgba(7, 0, 67, 0.7) 0%, rgba(4, 0, 36, 0) 100%);
  }
}

.main__bg-logo {
  position: absolute;
  width: 400px;
  left: 50%;
  top: 112px;
  transform: translateX(-50%);
  margin-left: 440px;
  z-index: 50;
}

.main__logo {
  margin-bottom: 85px;
  text-align: center;
}

.main__title {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 50px;
  line-height: 61px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;

  span {
    display: flex;
    justify-content: center;
    color: #C5C6E5;
    align-items: center;

    img {
      margin-right: 36px;
    }
  }
}

.main__description {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: left;
  max-width: 706px;
  margin: 0 auto;
  text-transform: none;
}

.main__bottom {
  position: absolute;
  left: 0;
  bottom: 23px;



  .container {
    display: flex;
    align-items: center;

    &:before {
      content: "";
      position: relative;
      width: 165px;
      display: block;
      height: 1px;
      background-color: #fff;
      margin-right: 50px;
    }
  }

  .socials {
    margin-bottom: 0px;
    margin-right: 60px;
  }
}

.slider-nav.slider-nav--main {
  padding-top: 0px;

  svg {
    fill: #fff;
    opacity: 1;
  }

  .slider-numbers {
    color: #fff;
  }

  .next,
  .prev {

    &:hover {
      svg {
        opacity: .5;
        fill: #fff;
      }
    }
  }
}



.header--main {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 50;
  background: transparent;

  .header__wrap {
    border-bottom: 1px solid rgba(217, 217, 217, 0.2);
    height: auto;
  }

  .search__input {
    background: none;
  }
}

.inner {
  padding-top: 30px;
  background: url(../img/inner-bg.svg) no-repeat right -60px top -299px / auto;
}

.scrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 68px;
}

.scrumbs__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding-right: 18px;
  margin-right: 17px;
  border-right: 1px solid #6A65A4;
  color: #6A65A4;
  transition: color .4s;

  &:last-child {
    margin-right: 0px;
    padding-right: 0px;
    border: none;
    color: #D9D9D9;

    &:hover {
      color: #6A65A4;
    }
  }
}

.about__list {
  display: flex;
  justify-content: space-between;

}


.ico-block {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;

  img {
    width: 54px;
    margin-right: 41px;
  }
}

.ico-text {
  width: calc(100% - 95px);
  font-size: 16px;
  line-height: 25px;
}

