}

.about__video-play {
  width: 547px;
  position: relative;



  a {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    &:hover .after svg {
      fill: #6A65A4;
    }

    .after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 93px;
      height: 93px;

      svg {
        filL: #fff;
        transition: .4s;
      }
    }
  }

  img {
    width: 100%;
  }
}

