      border-color: #6A65A4;
    }

    &.-is-focus {
      border-color: #6A65A4;
    }
  }
}

.form__flex {
  display: flex;
  justify-content: space-between;

  .input {
    width: calc(50% - 10px);
  }
}


.form__submit {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.form__submit-description {
  font-size: 14px;
  line-height: 25px;
  color: #fff;
  margin-right: 23px;
}

.activities-form {
  width: 3000px;

  &:before {
    content: "";
    position: absolute;
    width: 738px;
    height: 100%;
    left: 600px;
    top: -2px;
    background: url(../img/form-bg.svg) no-repeat top left / 100% auto;
  }
}


.input__error {
  position: absolute;
  top: 100%;
  left: 0;
