  }
}


.project-slider {
  border-left: 1px solid #D9D9D9;
}


.project {
  display: flex;
  margin-bottom: 60px;

  &:last-child {
    margin-bottom: 0px;
  }
}

.project__left {
  padding-top: 61px;
