}

.map {
  padding-top: 60px;
  padding-bottom: 81px;
  background: url(../img/inner-bg.svg) no-repeat left -220px top -299px / auto;

  .ico-block {
    position: absolute;
    right: 20px;
    bottom: 0;
    align-items: center;
  }
}

.map-point {
  cursor: pointer;
  position: relative;
}

.map-point {
  &.active {
    z-index: 1000;

    foreignObject {
      display: inline-block;
      position: relative;
      z-index: 1;
    }

    rect {
      position: relative;
      z-index: 50;
    }
  }

  foreignObject {
    width: 100vw;
    max-width: 280px;
    height: 83px;
    display: none;
    cursor: default;
  }
}

// x + 30
// y - 100

.map-point__message {
  left: 100%;
  bottom: 100%;
  max-width: 264px;
  width: 100vw;
  padding: 15px 48px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  border-top: 1px solid #18143E;
  background-color: #fff;
}

.map__pic {
  margin-top: -150px;

  svg {
    width: 100%;
  }
}


.anchor-arrow {
  position: absolute;
  z-index: 20;
  left: 50%;
  top: -5px;
  width: 31px;
  height: 31px;
  background-color: #6A65A4;
  transform: rotateZ(45deg) translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    transform: rotateZ(-45deg);
    position: relative;
    z-index: 10;
  }

  &:after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 46px;
    height: 46px;
    transform: translate(-50%, -50%);
    background-color: rgba(106, 101, 164, 0.3);
    z-index: 1;
  }
}

.--mobile {
  display: none;
}

.activities__mobile-main {
  display: none;
  padding-top: 24px;
  padding-bottom: 24px;
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  font-weight: 700;
  font-size: 15px;
  line-height: 18px;
  color: #6A65A4;
  position: relative;

  &.-is-active:after {
    transform: translateY(-50%) rotateZ(180deg);
  }

  &:after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%) rotateZ(0);
    width: 15px;
    height: 8px;
    background: url(../img/accardeon.svg) no-repeat center / contain;
  }
}


.slider-nav--map {
  display: none;
}


@media(max-width: 1300px) {
  .activities-form {
    width: calc(100% + 20px);
    padding-right: 20px;
  }

  #map {
    width: calc(100% + 40px);
    margin-left: -20px;
  }

  .news__left {
